import React, { useState } from "react";
import './App.css';
// import TodoForm from "./components/TodoForm.js";

const Todo = ({ list }) => <div className="todo">{list.text}</div>;

function TodoForm({ fff }) {
  const [value, setValue] = useState("");

  const handleSubmit = e => {
    e.preventDefault();
    if (!value) return;
    addTodo(value);
    setValue("");
  };

  return (
    <form onSubmit={handleSubmit}>
      <input
        type="text"
        className="input"
        value={value}
        onChange={e => setValue(e.target.value)}
      />
    </form>
  );
}

function App() {
  const [todos, setTodos] = useState([
    { text: "Learn about React" },
    { text: "Meet friend for lunch" },
    { text: "Build really cool todo app" }
  ]);

  const addTodo = text => {
    const newTodos = [...todos, { text }];
    setTodos(newTodos);
  };

  return (
    <div className="app">
      <div className="todo-list">
        {todos.map((r, index) => (
          <Todo
            key={index}
            index={index}
            list={r}
          />
        ))}
        <TodoForm fff={addTodo} />
      </div>
    </div>
  );
}

export default App;
